
.half-width{width:50%
}
.push-half-right{margin-left:50%
}
.nav a{font-size:15px
}
.dropdown-menu li a{padding:10px
}
.container{min-height:100%;position:relative
}
.bg-primary{background:#005b96 !important
}
.bg-secondary{background:#005b96 !important
}
.btn-primary{background:#005b96 !important;border-color:#005b96 !important;height:35px !important
}
.btn-primary:hover{background:#011f4b !important;border-color:#011f4b !important
}
.btn-primary:active{background:#011f4b !important;border-color:#011f4b !important
}
.btn-secondary{background:#e92424 !important;border-color:#e92424 !important;height:35px !important
}
.btn-secondary:hover{background:#e92424 !important;border-color:#e92424 !important
}
.btn-danger{background:red !important;height:35px !important
}
.btn-danger:hover{background:red !important;border-color:red !important
}
.btn-warning{background:#fc0 !important;height:35px !important
}
.text-primary{color:#005b96 !important
}
.text-white{color:#fff !important
}
.text-gray{color:#555 !important
}
.text-yellow{color:#fc0 !important
}
.page-link{color:#005b96 !important;background:inherit
}
.empty-table:hover{cursor:pointer;color:white !important;background:#005b96 !important
}
.page-link:hover{cursor:pointer;background:#005b96 !important;color:white !important
}
.action-link:hover{color:#005b96 !important;cursor:pointer
}
.text-underline:hover{cursor:pointer;text-decoration:underline
}
.breadcrumb{color:#005b96;background:inherit !important;border:none !important
}
.breadcrumb:hover{cursor:pointer;text-decoration:underline
}
td i{font-size:16px !important;padding-right:10px
}
td i:hover{cursor:pointer;color:#005b96 !important
}
.table tbody td{height:35px !important;padding:.50rem !important
}



